import {get, isEmpty} from 'lodash';

export function getFieldResource(resources) {
	return get(resources, 'field', {});
}

export function getData(data) {
	if (!data.AppLookupVerify_Query || !data.AppLookupVerify_Query.verificationResults)
		return 'No data found';

	return data.AppLookupVerify_Query.verificationResults;
}

export function getTitle(verificationFields, titleField) {
	if (!verificationFields)
		return;

	var newRecordArr = JSON.parse(JSON.stringify(verificationFields));

	let title = '';
	let avatar = '';
	
	if (isEmpty(verificationFields)) {
		return { displayFields: [],
				 title: title,
				 avatarImg: avatar};
	}

	//handle avatar if it's included in the record
	let avatarIndex = newRecordArr.findIndex( field => field.name === 'avatar' );
	if (avatarIndex > -1) {
		let avatarData = newRecordArr.splice(avatarIndex, 1);
		avatar = avatarData[0].displayValue ? avatarData[0].displayValue : avatarData[0].value;
	}

	// Lookup and Verify case: titleField will be '__title__'
	// Verify only case: titleField will be the first element in the array(once the avatar field is removed)
	let titleIndex = 0;
	if (titleField && newRecordArr.findIndex( field => field.name === titleField ) > -1)
		titleIndex = newRecordArr.findIndex( field => field.name === titleField );

	
	let titleFieldData = newRecordArr.splice(titleIndex, 1);
	if(titleFieldData && titleFieldData.length > 0)
		title = titleFieldData[0].displayValue ? titleFieldData[0].displayValue : '';
	
	return {displayFields: newRecordArr,
		title: title,
		avatarImg: avatar};
}

export function getRecord(data) {
	if (!data.verificationFields)
		return false;
	return data.verificationFields;
}

export function getValidField(record, fieldName) {
	if (!record[fieldName] || !record[fieldName].label)
	 return false;
	let field = get(record, fieldName);
	return field;
}

export function getFieldType(fieldData) {
	if (!fieldData.type)
		return 'String';
	return `${fieldData.type}`;
}

export function getFieldLabel(fieldData) {
	if (!fieldData.label)
		return 'Field label not found';
	return `${fieldData.label}`;
}

export function getFieldValue(fieldData) {
	return `${fieldData.displayValue}`;
}
