import { get, isNil } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { logErrorEffect } from '../effects';
import {actionTypes} from '@servicenow/ui-core';
import {
	LOOKUP_QUERY,
	LOAD_LOOKUP_REQUEST_STARTED,
	LOAD_LOOKUP_SUCCEEDED,
	LOAD_LOOKUP_FAILED,
	LOOKUP_SELECTION_CHANGED,
	SET_STATE
} from '../utils/constants';

const lookupQuery = `query($searchTerm: String!, $sysId: String!) {
 	AppLookupVerify_Query{
   		lookupVerify(searchTerm: $searchTerm, sys_id: $sysId){
     		lookupVerifyDataList{
       			value
       			primary
		        secondary
		        verificationFields{
		        	name
		        	label
		        	type
		        	value
		        	displayValue
		       	}
		    }
		    totalCount
		}
	}
}`;

const getOptionsFromLookupResults = (results, maxResultCount) => {
	var searchResults = [];
	var moreResults = false;

	if (results && Object.keys(results).length > 0) {
		if (results.length > maxResultCount) {
			moreResults = true;
		}

		for (var i = 0; i < results.length && i < maxResultCount; i++) {
			var optionValue = results[i].value;
			var displayValue = results[i].primary;
			var secondary = JSON.parse(results[i].secondary);
			var option = { value: optionValue, displayValue: displayValue, secondary: secondary };
			searchResults.push(option);
		}
	}

	return {
		moreResults: moreResults,
		searchResults: searchResults
	};
};

const fetchLookupEffect = createGraphQLEffect(lookupQuery, {
	variableList: ['sysId', 'searchTerm'],
	startActionType: LOAD_LOOKUP_REQUEST_STARTED,
	successActionType: LOAD_LOOKUP_SUCCEEDED,
	errorActionType: LOAD_LOOKUP_FAILED
});

const lookupSelectionChangedEffect =
	(({state, action, updateState}) => {
		var newSysId = state.sysId;
		if (!isNil(action.payload.sysId)) {
			newSysId = action.payload.sysId;
		}
		updateState({
			isFocused: action.payload.isFocused, 
			selectedEntityDisplayVal: action.payload.selectedEntityDisplayVal,
			sysId: newSysId
		});
	});

const lookupSucceededEffect =
	(({state, action, updateState}) => {
		const results = get(action, 'payload.data.AppLookupVerify_Query.lookupVerify.lookupVerifyDataList', []);
		const optionArray = getOptionsFromLookupResults(results, state.properties.maxResultCount);
		var newSysId = state.sysId;
		if (!isNil(action.payload.sysId)) {
			newSysId = action.payload.sysId;
		}
		updateState({
			searchResults: results,
			optionArray: optionArray,
			sysId: newSysId
		});
	});

const setStateEffect =
	(({state, action, updateState}) => {
		updateState({
			...action.payload
		});
	});

export const actionHandlers = {
	[LOOKUP_QUERY]: {
		...fetchLookupEffect,
		stopPropagation: true
	},
	[LOAD_LOOKUP_SUCCEEDED]: {
		effect: lookupSucceededEffect,
		stopPropagation: true
	},
	[LOAD_LOOKUP_FAILED]: {
		effect: logErrorEffect,
		stopPropagation: true
	},
	[LOOKUP_SELECTION_CHANGED]: {
		effect: lookupSelectionChangedEffect
	},
	[SET_STATE]: {
		effect: setStateEffect,
		stopPropagation: true
	},
	[actionTypes.COMPONENT_DOM_READY]: ({action, dispatch}) => {
		dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', false);
		var {host} = action.payload;
		var searchTermOnLoad = host.selectedEntityDisplayVal ? host.selectedEntityDisplayVal : '';
		var shouldSearchOnLoad = !host.selectedEntitySysId || host.selectedEntitySysId == ''  ? true : false;
		if (searchTermOnLoad.length > 0) {
			dispatch(LOOKUP_SELECTION_CHANGED, {
				sysId: host.selectedEntitySysId, 
				selectedEntityDisplayVal: searchTermOnLoad, 
				isFocused: shouldSearchOnLoad
			});
		}
	}
};
