import {t} from 'sn-translate';
import '@servicenow/now-avatar';
import '@servicenow/now-label-value';
import {isEmpty} from 'lodash';
import {getTitle} from './utilities';
import {
	TITLE_FIELD_NAME,
	LOAD_DISPLAY,
	LOOKUP_SELECTION_CHANGED
} from '../utils/constants';

const view = (state, {dispatch}) => {
	// verificationFields - from lookup component
	// verificationResult - from graphql result, sysId provided
	const {
		properties: { 
			verificationFields, 
			showAvatar, 
			selectedEntitySysId, 
			stringForLookup,
			sysId, 
			configId, 
			table,
			verificationColumns, 
			showVerificationBorder,
			showVerificationTitle },
		verificationResult, loading
	} = state;

	if (loading) {
		return (<div className="sn-widget">{t('Loading data...')}</div>);
	}

	let dataFields;
	if(isEmpty(verificationFields) && isEmpty(verificationResult)) {
		if (selectedEntitySysId) {
			dispatch(LOAD_DISPLAY, {sysId: selectedEntitySysId, configId: configId});
			dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', true);
			dispatch('SN_SCRIPTED_MODAL#DATA_SET', { sysId: selectedEntitySysId, configId: configId, stringForLookup: stringForLookup });
			return (<div className="sn-widget">{t('Loading data...')}</div>);
		} else {
			return null;
		}	
	} else {
		dataFields = (!isEmpty(verificationFields) ? verificationFields : verificationResult);
		if (isEmpty(verificationFields)) {
			dispatch(LOOKUP_SELECTION_CHANGED, {table, sysId});	
			
		}
		dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', true);
		dispatch('SN_SCRIPTED_MODAL#DATA_SET', { sysId: sysId, configId: configId, stringForLookup: stringForLookup });
	}

	let avatarElement = [];
	const {title, avatarImg, displayFields} = getTitle(dataFields, TITLE_FIELD_NAME);
	//	let displayTitle = isEmpty(selectedEntityDisplayVal) ? title : selectedEntityDisplayVal;
	if (showAvatar)
	{
		const avatarURL = (avatarImg != null ? avatarImg.substring(1, avatarImg.length) : '');
		avatarElement.push(<now-avatar size="md" user-name={title} image-src={avatarURL}></now-avatar>);
	}

	let tableElement = [];
	let columns = [];
	for (let i=0; i<verificationColumns; i++) {
		columns.push([]);
	}

	displayFields.forEach(function(element, index) {
		let colIndx = index % verificationColumns;
		let column = columns[colIndx];

		let fieldDisplayName = element.label;
		let fieldValue = element.displayValue ? element.displayValue : element.value;
		let value = fieldValue && fieldValue != 'null' ? fieldValue : t('No data');

		column.push({
			label: fieldDisplayName,
			value: value
		});
	});

	columns.forEach(function(colItems) {
		tableElement.push(
			<td>
				<now-label-value-stacked
					items={colItems}
					align="vertical"
					size="sm"
					wrapped
					/>
			</td>
		);
	});

	const displayDivClass = showVerificationBorder ? 'sn-widget verification-border' : 'sn-widget';

	return (
		<div className="verification-card">
			<div className={displayDivClass}>
				<div id="verify_avatar" className="sn-widget-content -static">
					{avatarElement}
				</div>
				<div className="sn-widget-content">
					{showVerificationTitle ? <div className="sn-widget-username" role="heading" aria-level="2" id="verify_title">{title}</div> : ''}
					<table role="presentation">
						<tbody>
							<tr>
								{tableElement}
							</tr>
						</tbody>
					</table>
				</div>
			</div>	
		</div>
	);
};

export default view;