import '../snComponentDisplay';
import '../snComponentLookup';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';
import {toAttributes} from '../utils/utils';
import {
	LOAD_LOOKUP_VERIFY
} from '../utils/constants';

const view = (state, {dispatch}) => {
	const {
		properties: {
			selectedEntityDisplayVal, 
			configId, 
			selectedEntityTableName, 
			selectedEntitySysId,
			verificationColumns,
			showVerificationBorder,
			showVerificationTitle,
			lookupFieldPlaceholder,
			showLookupFieldLabel,
			showAvatar},
	 	loading, verificationFields, stringForLookup, minCharsToStartSearch, maxResultCount, configLoaded, sysId} = state;

	if (!configLoaded && !loading) {
		dispatch(LOAD_LOOKUP_VERIFY, {sysId: configId});
		return (<div>{t('Loading data...')}</div>);
	}

	if (loading) {
		return (<div>{t('Loading data...')}</div>);
	}
	const lookupAttrsJson = {
		configId,
		selectedEntityDisplayVal,
		selectedEntitySysId,
		minCharsToStartSearch,
		maxResultCount,
		lookupFieldPlaceholder,
		showLookupFieldLabel
	};
	const displayAttrsJson = {
		selectedEntitySysId: selectedEntitySysId,
		sysId: sysId,
		configId,
		stringForLookup: stringForLookup,
		table: selectedEntityTableName,
		verificationFields,
		showAvatar,
		verificationColumns,
		showVerificationBorder,
		showVerificationTitle,
	};
	const lookupAttrs = toAttributes(lookupAttrsJson);
	const displayAttrs = toAttributes(displayAttrsJson);
	return (
		<Fragment>
		    <div className="sn-lookup-verify">
				<sn-component-workspace-lookup {...lookupAttrs} />
				<sn-component-workspace-display {...displayAttrs} />
			</div>	
		</Fragment>	
	);
};

export default view;