import {get} from 'lodash';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {logErrorEffect} from '../effects';
import {getTitle} from './utilities';
import {
	LOAD_DISPLAY,
	LOAD_DISPLAY_REQUEST_STARTED,
	LOAD_DISPLAY_SUCCEEDED,
	LOAD_DISPLAY_FAILED
} from '../utils/constants';

const fieldQuery = `
query($sysId: String!, $configId: String!) {
  AppLookupVerify_Query{
    verificationResults(sys_id: $sysId,config_id: $configId) {
      verificationFields {
        name,
        label,
        type,
        value,
        displayValue,
      }
    }
  }
}
`;

const fetchFieldEffect = createGraphQLEffect(fieldQuery, {
	variableList: ['sysId', 'configId'],
	startActionType: LOAD_DISPLAY_REQUEST_STARTED,
	successActionType: LOAD_DISPLAY_SUCCEEDED,
	errorActionType: LOAD_DISPLAY_FAILED
});

export const actionHandlers = {
	[LOAD_DISPLAY]:{
		...fetchFieldEffect,
		stopPropagation: true
	},
	[LOAD_DISPLAY_REQUEST_STARTED]:{ 
        effect: ({ action, state, updateState }) => {
			updateState({
				loading: true
			})
		},
		stopPropagation: true
	},
	[LOAD_DISPLAY_SUCCEEDED]:{ 
        effect: ({ action, state, updateState }) => {
        	let verificationFields = get(action, 'payload.data.AppLookupVerify_Query.verificationResults.verificationFields', []);
			const {title} = getTitle(verificationFields, 'name');
        	updateState({
        		loading: false,
        		verificationResult: verificationFields,
        		title: title,
			});},
		stopPropagation: true
	},
	[LOAD_DISPLAY_FAILED]: logErrorEffect
};