import {get, isNil} from 'lodash';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {logErrorEffect} from '../effects';
import {
	LOAD_LOOKUP_VERIFY,
	LOAD_LOOKUP_VERIFY_REQUEST_STARTED,
	LOAD_LOOKUP_VERIFY_SUCCEEDED,
	LOAD_LOOKUP_VERIFY_FAILED,
	LOOKUP_SELECTION_CHANGED
} from '../utils/constants';

const configQuery = `query($sysId :String!){
	AppLookupVerify_Query{
  		lookupConfiguration(sys_id: $sysId){
    		searchForConfig{
		    	active
		      	minCharsToStartSearch
		      	maxResultCount
		    }
  		}
	}
}`;

const fetchConfigEffect = createGraphQLEffect(configQuery, {
	variableList: ['sysId'],
	startActionType: LOAD_LOOKUP_VERIFY_REQUEST_STARTED,
	successActionType: LOAD_LOOKUP_VERIFY_SUCCEEDED,
	errorActionType: LOAD_LOOKUP_VERIFY_FAILED
});

const lookupSelectionChanged = ({state, action, updateState}) => {
	var newVerificationFields = state.verificationFields;
	if (!isNil(action.payload.verificationFields)) {
		newVerificationFields = action.payload.verificationFields;
	} 

	updateState({
		sysId: action.payload.sysId,
		table: action.payload.table,
		stringForLookup: action.payload.stringForLookup,
		verificationFields: newVerificationFields
	});
};

const loadLookupVerifySucceeded = ({state, action, updateState}) => {
	let config = get(action, 'payload.data.AppLookupVerify_Query.lookupConfiguration.searchForConfig');
	updateState({
		active: config.active,
		minCharsToStartSearch: config.minCharsToStartSearch,
		maxResultCount: config.maxResultCount,
		loading: false,
		configLoaded: true
	});
};

export const actionHandlers = {
	[LOAD_LOOKUP_VERIFY]: {
		...fetchConfigEffect,
		stopPropagation: true
	},
	[LOAD_LOOKUP_VERIFY_SUCCEEDED]: {
		effect: loadLookupVerifySucceeded,
		stopPropagation: true
	},
	[LOAD_LOOKUP_VERIFY_FAILED]: {
		effect: logErrorEffect,
		stopPropagation: true
	},
	[LOOKUP_SELECTION_CHANGED]: {
		effect: lookupSelectionChanged,
		stopPropagation: true
	}
};