import { get } from 'lodash';
import { t } from 'sn-translate';
import { onSearchTermChange, onSelectionChange } from './utilities';
import '@servicenow/now-record-typeahead';

const view = (state, {dispatch}) => {
	const {
		properties: { configId, minCharsToStartSearch,
			lookupFieldPlaceholder, showLookupFieldLabel, maxResultCount, selectedEntityDisplayVal: verifyDisplayVal },
		searchResults, searchTerm, stringForLookup, selectedEntitySysId,
		noResultsFoundMessage, isFocused,
		selectedEntityDisplayVal, timeout,
		optionArray } = state;
	
	const value = selectedEntityDisplayVal.length > 0 ? selectedEntityDisplayVal : searchTerm;
	const optionArraySearchResults = get(optionArray, 'searchResults', []);
	var moreResults = get(optionArray, 'moreResults', false);
	const label = showLookupFieldLabel ? t('Look up by name, phone, or record number') : '';
	var loadingMessage = t('Searching...');
	var totalMatchesCountHeaderMessage = t('{0} results', searchResults.length);
	var noResultsFoundMessageDisplay = noResultsFoundMessage;

	if (value.length == 0)
		loadingMessage = t('Start typing to see results');
	else if (value.length < minCharsToStartSearch)
		loadingMessage = t('Continue typing to see results');
	else if (optionArraySearchResults.length == 0)
		noResultsFoundMessageDisplay = t('No Results Found');

	if (searchResults.length == 1)
		totalMatchesCountHeaderMessage = t('1 result');
	else if (moreResults)
		totalMatchesCountHeaderMessage = t('Showing {0} of many results. Refine your search terms', maxResultCount);

	
	return (
		<now-record-typeahead
			label={label}
			name="snLookupReference"
			value={value}
			placeholder={lookupFieldPlaceholder}
			options={optionArraySearchResults}
			onValueChange={(a, b, selectionValue, selectionDisplayValue, e) => {
				onSelectionChange(selectionValue, selectionDisplayValue, searchResults, dispatch, stringForLookup);
			}}
			onChange={
				(event) => {
					onSearchTermChange(event, configId, dispatch, timeout, minCharsToStartSearch,
						selectedEntityDisplayVal);
				}}
			optiontemplate="user"
			loadingMessage={loadingMessage}
			noResultsFoundMessage={noResultsFoundMessageDisplay}
			totalMatchesCountHeaderMessage={totalMatchesCountHeaderMessage}
			initialActive={isFocused}
		/>
	);
};

export default view;