import { t } from 'sn-translate';
import { get } from 'lodash';
import {
	LOOKUP_QUERY,
	LOOKUP_SELECTION_CHANGED,
	SET_STATE,
	TITLE_FIELD_NAME
} from '../utils/constants';

const parseSelectedValue = (selectionValue) => {
	var selectionSplit = selectionValue.split(':');
	return { table: selectionSplit[0], sys_id: selectionSplit[1] };
};

const debounceSearch = (searchTerm, timeout, minCharsToStartSearch, dispatch, configId) => {
	//Trim the leading spaces on the search term before searching
	var searchTerm = searchTerm.replace(/^\s+/g, '');
	if (timeout) {
		clearTimeout(timeout);
		dispatch(SET_STATE, { optionArray: [], searchResults: [] });
	}

	if (searchTerm.length >= minCharsToStartSearch) {
		var _this = this;
		timeout = setTimeout(function () {
			dispatch(LOOKUP_QUERY, {
				searchTerm: searchTerm,
				sysId: configId
			});
		}, 250);
		dispatch(SET_STATE, { noResultsFoundMessage: t('Searching...'), timeout });
	} else {
		dispatch(SET_STATE, {
			optionArray: [],
			searchResults: [],
			noResultsFoundMessage: t('Continue typing to see results')
		});
	}
};

export const onSearchTermChange = (event, configId, dispatch, timeout, minCharsToStartSearch, selectedEntityDisplayVal) => {
	if (event) {
		let newState = {};
		var newSearchTerm = event.target.value.replace(/^\s+/g, '');
		if (selectedEntityDisplayVal != null && selectedEntityDisplayVal.length > 0) {
			newState.selectedEntityDisplayVal = '';
		}
		debounceSearch(newSearchTerm, timeout, minCharsToStartSearch, dispatch, configId);
		newState.searchTerm = newSearchTerm;
		newState.stringForLookup = event.target.value;
		dispatch(SET_STATE, newState);
	}
};

export const onSelectionChange = (selectionValue, selectionDisplayValue, searchResults, dispatch, stringForLookup) => {
	const { table, sys_id } = parseSelectedValue(selectionValue);
	if (sys_id == null || sys_id.length == 0)
		return;
	let verificationResult;
	if (searchResults && Object.keys(searchResults).length > 0) {
		for (var i = 0; i < searchResults.length; i++) {
			if (searchResults[i].value == selectionValue) {
				verificationResult = searchResults[i].verificationFields;
				var field = {
					'name': TITLE_FIELD_NAME,
					'displayValue': searchResults[i].primary
				};
				verificationResult.push(field);
				break;
			}
		}
	}
	dispatch(LOOKUP_SELECTION_CHANGED, {
		table: table,
		sysId: sys_id,
		searchTerm: selectionDisplayValue,
		selectedEntityDisplayVal: selectionDisplayValue,
		isFocused: false,
		verificationFields: verificationResult,
		stringForLookup: stringForLookup
	});
};

